import React from 'react';

import styles from './styles.scss';

export interface LabelProps {
  /** Текст */
  text: string;

  /** Мультиселект открыт? */
  isOpenDropdown: boolean;

  /** Выбранные элементы */
  activeValue?: string[];

  /** Текст плейсхолдера */
  placeholder?: string;

  /** Элемент активен или нет? */
  disabled?: boolean;
}

/** Лейбл для множественного выбора */
export default function Label({
  placeholder,
  isOpenDropdown,
  activeValue,
  text,
  disabled = false
}: LabelProps): JSX.Element | null {
  /** Если заблокирован, не отображать ничего */
  if (disabled) return null;

  /** Если что-то выбрано, отрисовать лейбл или ничего. Лейбл рисуется при закрытом списке  */
  if (activeValue?.length) {
    return isOpenDropdown ? null : <span>{ text }</span>;
  }

  if (!placeholder) return null;

  /** Если ничего не выбрано, показать placeholder (вне зависимости от состояния) */
  return (
    <span className={ styles.placeholder }>
      { placeholder }
    </span>
  );
}
